@mixin desktop-landscape {
  //--|ðŸ Š display: flex; ðŸ ˆ|--//
  display: inherit; //~~|ðŸ Š display: grid; ðŸ ˆ|~~//
  border-radius: 1rem;
  grid-area: 2 / 3 / 3 / 4; //~~|ðŸ Š row-start / col-start / row-end / col-end ðŸ ˆ|~~//
  background: rgba(black, 0.75);
  grid-template: 1fr auto 1fr / 1fr; //~~|ðŸ Š rows / columns ðŸ ˆ|~~//
  div[class*='header'] {
    display: inherit; //~~|ðŸ Š display: grid; ðŸ ˆ|~~//
    grid-area: 1 / 1 / 2 / 2; //~~|ðŸ Š row-start / col-start / row-end / col-end ðŸ ˆ|~~//
    grid-template: 4rem 1fr / 4rem 1fr 4rem; //~~|ðŸ Š rows / columns ðŸ ˆ|~~//
    // background: rgba(red, 0.25);
    div[class*='logo'] {
      display: flex;
      justify-content: center;
      grid-area: 1 / 1 / 2 / 2; //~~|ðŸ Š row-start / col-start / row-end / col-end ðŸ ˆ|~~//
      img {
        width: 100%;
        margin: 0.5rem;
      }
    }
    div[class*='label'] {
      grid-area: 1 / 2 / 2 / 3;
      h6 {
        margin: 0;
        width: 100%;
        color: white;
        padding: 0.5rem;
        text-align: center; //~~|ðŸ Š Ensures Text is Centered ðŸ ˆ|~~//
      }
    }
    button[class*='close'] {
      background: none;
      grid-area: 1 / 3 / 2 / 4; //~~|ðŸ Š row-start / col-start / row-end / col-end ðŸ ˆ|~~//
      img {
        width: 3rem;
        height: 3rem;
        padding: 0.5rem;
      }
    }
    div[class*='text'] {
      display: flex;
      align-items: center;
      grid-area: 2 / 2 / 3 / 3;
      h4 {
        padding: 4px;
        line-clamp: 3; //~~|ðŸ Š Standard property ðŸ ˆ|~~//
        color: white;
        text-wrap: balance;
        text-align: center; //~~|ðŸ Š Justifies text alignment ðŸ ˆ|~~//
        display: -webkit-box; //~~|ðŸ Š Enables multi-line truncation ðŸ ˆ|~~//
        -webkit-line-clamp: 4; //~~|ðŸ Š Limits the number of lines  ðŸ ˆ|~~//
        text-overflow: ellipsis; //~~|ðŸ Š Adds ellipsis for overflow ðŸ ˆ|~~//
        -webkit-box-orient: vertical; //~~|ðŸ Š Vertical Text ðŸ ˆ|~~//
      }
    }
  }
  div[class*='inputs'] {
    display: flex;
    flex-direction: column;
    grid-area: 2 / 1 / 3 / 2; //~~|ðŸ Š row-start / col-start / row-end / col-end ðŸ ˆ|~~//

    // background: rgba(green, 0.25);
    input {
      padding: 0.25rem 0.75rem;
      margin: 1rem 2rem;
    }
  }
  div[class*='footer'] {
    display: inherit; //~~|ðŸ Š display: grid; ðŸ ˆ|~~//
    grid-area: 3 / 1 / 4 / 2; //~~|ðŸ Š row-start / col-start / row-end / col-end ðŸ ˆ|~~//
    grid-template: 1fr 1fr / repeat(2, 1fr); //~~|ðŸ Š rows / columns ðŸ ˆ|~~//
    // background: rgba(blue, 0.25);
    mark[class*='action'] {
      display: inherit; //~~|ðŸ Š display: grid; ðŸ ˆ|~~//
      grid-area: 1 / 1 / 2 / 3; //~~|ðŸ Š row-start / col-start / row-end / col-end ðŸ ˆ|~~//
      grid-template: 1fr auto 1fr / repeat(2, 1fr); //~~|ðŸ Š rows / columns ðŸ ˆ|~~//
      background: none;
      .login-button {
        grid-area: 2 / 1 / 3 / 2; //~~|ðŸ Š row-start / col-start / row-end / col-end ðŸ ˆ|~~//
      }
      div[class*='message'] {
        display: flex;
        align-items: center;
        grid-area: 2 / 2 / 3 / 3; //~~|ðŸ Š row-start / col-start / row-end / col-end ðŸ ˆ|~~//

        h6 {
          margin: 0;
          width: 100%;
          color: white;
          text-align: center;

          // background: darkblue;
        }
      }
      button {
        h6 {
          cursor: pointer;
          color: black;
        }
        margin: 1rem;
        justify-content: center;
        padding: 0.75rem 0 0.5rem 0; //~~|ðŸ Š padding: Top Right Bottom Left ðŸ ˆ|~~//
      }
    }
    menu[class*='buttons'] {
      display: inherit; //~~|ðŸ Š display: grid; ðŸ ˆ|~~//
      grid-area: 2 / 1 / 3 / 3; //~~|ðŸ Š row-start / col-start / row-end / col-end ðŸ ˆ|~~//
      grid-template: 1fr auto 1fr / repeat(2, 1fr); //~~|ðŸ Š rows / columns ðŸ ˆ|~~//
      // background: blue;
      button[class*='register'] {
        grid-area: 2 / 1 / 3 / 2; //~~|ðŸ Š row-start / col-start / row-end / col-end ðŸ ˆ|~~//
      }
      button[class*='password'] {
        grid-area: 2 / 2 / 3 / 3; //~~|ðŸ Š row-start / col-start / row-end / col-end ðŸ ˆ|~~//
      }
      button {
        h6 {
          cursor: pointer;
          color: black;
        }
        margin: 1rem;
        background: white;
        justify-content: center;
        padding: 0.75rem 0 0.5rem 0; //~~|ðŸ Š padding: Top Right Bottom Left ðŸ ˆ|~~//
      }
    }
  }
}
