@mixin mobile-portrait {
  //--|ðŸ Š display: grid; ðŸ ˆ|--//
  // grid-template: 6rem 1fr 6rem / 1fr; //~~|ðŸ Š rows / columns ðŸ ˆ|~~//
  /*
  */
