aside[id*='rightbar'] {
  display: grid;
  grid-area: 1 / 1 / 4 / 4; //~~|ðŸ Š row-start / col-start / row-end / col-end ðŸ ˆ|~~//
  &[class*='collapsed'] {
    transition: clip-path 0.25s;
    clip-path: inset(0% 0% 0% 100%); //~~|ðŸ ˆ top / right / bottom / left ðŸ ˆ|~~//
  }
  /* Target elements with a class containing 'expanded' */
  &[class*='expanded'] {
    transition: clip-path 0.25s;
    clip-path: inset(0% 0% 0% 0%); //~~|ðŸ ˆ top / right / bottom / left ðŸ ˆ|~~//
  }

  //--|ðŸ ‹ Mobile (Portrait) ðŸ ‹|--//
  @media screen and (orientation: portrait) {
    @include mobile-portrait();
  }
  //--|ðŸ ‹ Desktop (Landscape) ðŸ ‹|--//
  @media screen and (orientation: landscape) {
    @include desktop-landscape();
  }
}
