@mixin desktop-landscape {
  //--|ðŸ Š display: grid; ðŸ ˆ|--//
  grid-template: 4rem / repeat(2, 1fr); //~~|ðŸ Š rows / columns ðŸ ˆ|~~//
  menu {
    display: flex;
    grid-area: 1 / 2 / 2 / 3;
    justify-content: space-around;
  }
}
@mixin mobile-portrait {
  //--|ðŸ Š display: grid; ðŸ ˆ|--//
  grid-template: 6rem / 2rem 1fr 2rem; //~~|ðŸ Š rows / columns ðŸ ˆ|~~//
  menu {
    grid-area: 1 / 2 / 2 / 3; //~~|ðŸ ˆ row-start / col-start / row-end / col-end ðŸ ˆ|~~//
  }
}

footer[id*='resume'] {
  display: grid;
  grid-area: 3 / 1 / 4 / 4; //~~|ðŸ Š row-start / col-start / row-end / col-end ðŸ ˆ|~~//

  //--|ðŸ ‹ Mobile (Portrait) ðŸ ‹|--//
  @media screen and (orientation: portrait) {
    @include mobile-portrait();
  }
  //--|ðŸ ‹ Desktop (Landscape) ðŸ ‹|--//
  @media screen and (orientation: landscape) {
    @include desktop-landscape();
  }
}
