@mixin desktop-landscape {
  //--|ðŸ ‹ Desktop (Landscape) ðŸ ‹|--//
  @media screen and (orientation: landscape) {
    //--|ðŸ Š display: grid; ðŸ ˆ|--//
    grid-template: 4rem / auto 1fr; //~~|ðŸ ˆ rows / columns ðŸ ˆ|~~//
    .signature-adjacent {
      height: 2rem;
      margin: 1rem 3rem;
    }
    menu {
      grid-area: 1 / 2 / 2 / 3; //~~|ðŸ ˆ row-start / col-start / row-end / col-end ðŸ ˆ|~~//
    }
  }
}
@mixin mobile-portrait {
  //--|ðŸ ‹ Mobile (Portrait) ðŸ ‹|--//
  @media screen and (orientation: portrait) {
    grid-template: repeat(3, 1fr) / repeat(3, 1fr); //~~|ðŸ Š rows / columns ðŸ ˆ|~~//
    .signature-adjacent {
      height: 75%;
      grid-area: 2 / 2 / 3 / 3;
    }
  }
}
header[id*='resume'] {
  display: grid;
  grid-area: 1 / 1 / 2 / 4; //~~|ðŸ Š row-start / col-start / row-end / col-end ðŸ ˆ|~~//
  @include mobile-portrait();
  @include desktop-landscape();
}

